import styled from "styled-components"
import child from '../assets/child.png'

const Container = styled.div`
position: fixed;
// top: 3.5rem;
width: 100%;
height:100vh;
// border: 2px solid red;
color: ${({ theme }) => theme.text};
display: flex;
justify-content:center;
align-items: center;

@media (max-width: 700px){
    // top: 1rem;

   
}

`

const Box = styled.div`
display: flex;
width:80%;
height: 60vh;
// border: 2px solid red;





&>:first-child{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1{
        font-size: 3rem;
        color: ${({ theme }) => theme.default};

    }

    p{
        font-size: 1.2rem;
        font-weight: 700;
        color: ${({ theme }) => theme.main};

        span{
            color: ${({ theme }) => theme.default};

        }

    }
   
  
    @media (max-width: 700px){

        h1{
            font-size: 2rem;    
        }
    
        p{
            font-size: .8rem;   
        }
    }

}



@media (max-width: 700px){
    width:100%;
    padding: 0 2rem;
   
}


`

const Content = styled.div`
width:50%;
position:relative;
// border: 2px solid red;
height:100%;

p{
    text-align: center
}

  

    .pic{
        position: absolute;
        // border: 2px solid green;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        object-fit: contain;
        width:100%;
        height:100%;
    
        
    
    
    }


    @media (max-width: 700px){
   
    }



`



export default function Home() {
    return <Container>
        <Box>
            <Content>
                <h1>Friends</h1>
                <p>Connect with <span>friends</span></p>
            </Content>

            <Content>
                <img src={child} alt="" className="pic" />

            </Content>
        </Box>
    </Container>
}