import axios from "axios"
import { Suspense, useEffect, useRef, useState } from "react"
import styled from "styled-components"
import { ChatBox, Contact, Welcome, ChatOnlineUsers, useStateVal } from "../import"
import { getAllUser } from "../utils/Api"

const Container = styled.div`
width: 100%;
height:100vh;
display: flex;
background: ${({ theme }) => theme.body};
padding-top: 4rem;


&>:first-child{
    // border:2px solid red;
    flex:3.5;
  
   
}

div&>:nth-child(2){
    // border:2px solid green;
    flex:5.5
}


&>:last-child{
    // border:2px solid yellow;
    flex:3.5;

    @media(max-width: 700px){
        display: none;

       
        
       
    }
}

`
const ContactContainer = styled.div`
@media (max-width: 700px){
    display: ${props => (props.ham === true) ? "block" : "none"};
    flex: 1,
}

`

const ChatBoxContainer = styled.div`
@media (max-width: 700px){
    display: ${props => (props.ham === true) ? "none" : "block"};
}

`



export default function Chat() {
    const [contacts, setContacts] = useState([])
    const [currentChat, setCurrentChat] = useState(null)
    const [loading, setLoading] = useState(true)
    let currentRef = useRef(false)
    const { ham, setHam } = useStateVal()

    // console.log(ham);


    const chooseChatDisplayed = (chat) => {
        setCurrentChat(chat)
        setHam(false)
    }

    const allUsers = async () => {
        try {
            const { data } = await axios.get(getAllUser)
            if (data.success) {
                // console.log('allusers',data.users);
                setContacts(data.users)
                setLoading(false)
            }
        } catch (error) {
            console.log(error);

        }
    }

    useEffect(() => {
        if (currentRef.current) return;

        allUsers()

        currentRef.current = true;

    }, [])



    return <Container>
        <ContactContainer ham={ham}>
            <Suspense fallback={<p>loading....</p>}>
                <Contact contacts={contacts} chooseChatDisplayed={chooseChatDisplayed} />

            </Suspense>

        </ContactContainer>
        <ChatBoxContainer ham={ham}>
            {!loading && currentChat ? <ChatBox currentChat={currentChat} /> : <Welcome />}

        </ChatBoxContainer>

        <div>
            <ChatOnlineUsers contacts={contacts} chooseChatDisplayed={chooseChatDisplayed} />

        </div>
    </Container>
}