import { useEffect, useRef, useState } from "react"
import styled from "styled-components"
import Picker from 'emoji-picker-react'
import axios from 'axios'
import { sendMsg } from '../utils/Api'
import { useStateVal } from "../import"
import Tone from '../assets/messaging.mp3'

const Container = styled.div`

height: 100%;
display: grid;
grid-template-columns: 5% 95%;
align-items: center;
// padding:0 2rem;
border: .1px solid  ${({ theme }) => theme.text};
border-radius: 1rem;

.btn{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    .emoji{
        position: relative;
        margin-left: .5rem;
        h3{
            font-size: 1rem;
            cursor: pointer;
        }
        .emoji-picker-react{
            position:absolute;
            top: -350px;
            background: ${({ theme }) => theme.body};
            border-color: grey;
            box-shadow: 0 5px 10px grey;
            .emoji-scroll-wrapper::-webkit-scrollbar{
                width: .2rem;
                &-thumb{
                    background: blue;
                    width: .1rem;
        
        
                }
            }



            .emoji-categories{
                button{
                    filter: contrast(0)
                }
            }
            .emoji-search{
               background: transparent;
            }
            .emoji-group:before{
                background: ${({ theme }) => theme.body};

            }



            @media(max-width: 700px){
                height: 200px;
                width: 200px;
                    top: -220px;
            }
        }
    }
}

form{
    width: 100%;
    display: flex;

    input{
        flex: .9;
        height: 60%;
        background: transparent;
        color: white;
        outline: none;
        border:none;
        // border: .1px solid  ${({ theme }) => theme.text};
        // border-radius: 1rem;
        padding: 1.2rem;
        
        // &:focus{
        //     border: 0.1rem solid  ${({ theme }) => theme.text};
        //     outline: none;

        // }
    }
    input::placeholder{
        // color:grey;
        font-size: 1.2rem;

        @media(max-width: 700px){
    
            font-size: .8rem;
    
            
           
        }

    }
    button{
        flex:.1;
        border: .1px solid  ${({ theme }) => theme.body};
        cursor: pointer;
        padding: 1.2rem 2rem;
        border-radius: 10px;
        text-align: center;
    }
}

@media(max-width: 700px){
margin: 0 .5rem;
}
   
`
export default function ChatInput({ currentChat, messages, setMessages, setTyping }) {
    const [text, setText] = useState("")
    const [chosenEmoji, setChosenEmoji] = useState(false)
    const { user, socket, arrivedMessage, setArrivedMessage, setNotification } = useStateVal()

    const ToggleEmoji = () => setChosenEmoji(!chosenEmoji)
    // console.log(socket);
    const messageTone = new Audio(Tone)
    const inputRef = useRef()


    const onEmojiClick = (e, emojiObject) => {
        let message = text
        message += emojiObject.emoji
        setText(message)
        // setChosenEmoji(emojiObject)
    }

    const handleChange = e => {
        setText(e.target.value)

    }

    // console.log('currentChat', currentChat);
    // console.log('user', user._id);
    const sendMessage = async (msg) => {
        // console.log(msg);
        try {
            const { data } = await axios.post(sendMsg, {
                message: msg,
                to: currentChat._id,
                from: user._id
            })
            // console.log(data);
            if (data.success) {
                socket.current.emit("sendMessage", {
                    message: msg,
                    to: currentChat._id,
                    from: user._id,
                    date: data.data.createdAt
                })

                //notification
                socket.current.emit("sendNotification", {
                    message: msg,
                    to: currentChat._id,
                    toName: currentChat.name,
                    from: user._id,
                    fromName: user.name,
                    date: data.data.createdAt
                })

                setText("")

                const msgs = [...messages]
                // console.log("messages", messages);
                // console.log("msgs", msgs);
                // console.log("TIME", new Date(data.data.createdAt).toLocaleTimeString());

                msgs.push({
                    fromSelf: true,
                    message: {
                        text: msg
                    },
                    date: new Date(data.data.createdAt).toLocaleTimeString()

                })
                setMessages(msgs)
            }




        } catch (error) {
            console.log(error);
        }

    }

    const handleSubmit = (e) => {
        e.preventDefault()
        setChosenEmoji(false)
        if (text) {
            sendMessage(text)
        }

    }

    useEffect(() => {
        // console.log(socket.current);
        if (socket.current) {
            socket.current.on("msg-received", (msg) => {
                // console.log(msg);
                // setArrivedMessage({
                //     fromSelf: false,
                //     message: {
                //         text: msg
                //     }
                // })
                setArrivedMessage({
                    fromSelf: false,
                    message: {
                        text: msg.message
                    },
                    date: new Date(msg.date).toLocaleTimeString()
                })
                messageTone.play()

            })
        }

    }, [messages])

    useEffect(() => {
        arrivedMessage && setMessages((prev) => [...prev, arrivedMessage])

    }, [arrivedMessage])


    useEffect(() => {
        // if(arrivedMessage){
        socket.current.on("receiveNotification", (n) => {
            console.log(n);
            setNotification((prev) => [...prev, n])
        })

        // }
    }, [arrivedMessage])


    // useEffect(() => {
    //     inputRef.current.addEventListener("focus",(e) => {
    //         // console.log("typing");
    //         socket.current.emit("typing", {
    //             message: `${user.name} is typing`,
    //             to: currentChat._id,
    //             from: user._id
    //         })
    //     })


    //     inputRef.current.addEventListener("blur",(e) => {
    //         // console.log("typing");
    //         socket.current.emit("typing", {
    //             message: "",
    //             to: currentChat._id,
    //             from: user._id
    //         })
    //     })


    //     return () => {
    //         inputRef.current.removeEventListener("focus",(e) => {
    //             console.log("removed typing");
    //             // socket.current.emit("typing", {
    //             //     message: `${user.name} is typing`,
    //             //     to: currentChat._id,
    //             //     from: user._id
    //             // })
    //         })

    //     }


    // },[])








    // console.log('messages', messages);
    // console.log('arrivedMessage', arrivedMessage);




    return <Container>
        <div className="btn">
            <div className="emoji" >
                <h3 onClick={ToggleEmoji}></h3>
                {chosenEmoji && <Picker onEmojiClick={onEmojiClick} />}
            </div>
        </div>
        <form onSubmit={handleSubmit}>
            <input ref={inputRef} type="text" value={text} placeholder=" Message" onChange={handleChange} onFocus={() => setChosenEmoji(false)} />
            <button>Enter</button>

        </form>
    </Container>
}