import { useStateVal } from "../import";
import styled from "styled-components";
import { useState } from "react";
import image from '../assets/user.png'

const Container = styled.div`
width:100%;
height:100%;
background: ${({ theme }) => theme.body};
display: grid;
grid-template-rows :10% 75% 15%;
overflow: hidden;
border-right: 1px solid rgba(255, 255, 255, 0.2);
border-left: 1px solid rgba(255, 255, 255, 0.2);
color:   ${({ theme }) => theme.text};

h3{
    text-transform: uppercase;
    font-size: .8rem;

}

.input{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .chatcontactinput{
        width:90%;
        padding: .5rem 0;
        border: none;
        outline: none;
        background:transparent;
        border-bottom: 1px solid  ${({ theme }) => theme.text};
    }
    
    .chatcontactinput::placeholder{
        color:   ${({ theme }) => theme.text};
      
    }



}

.contacts{
    display: flex;
    flex-direction: column;
    align-items: center;
    // border: 4px solid green;
    overflow: auto;
    padding:1rem 0;
    gap: .5rem; 

    &::-webkit-scrollbar{
        // display: none
        width: .2rem;
        &-thumb{
            background: rgba(255, 255, 255, 0.2);
            width: .1rem;


        }
    }



    @media(max-width: 700px){

        h3{
            font-size: .6rem; 
        }
        
       
    }


    

  
}

.currentuser{
    border: 4px solid  rgba(255, 255, 255, 0.2) ;
    background: ${({ theme }) => theme.body};
    display: flex;
    align-items: center;
    justify-content: center;

    .avatar{
        border-radius: 50%;
        width: 3em;
        height:3em;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        margin-right:.6rem;

        img{
            width: 100%;
            height: 100%;

        }
    }

    @media(max-width: 700px){
        width: 90%;   
       
    }
   
}


@media(max-width: 700px){
    .currentuser{
        margin 0 auto .5rem;
    }
}

`

const ContactContainer = styled.div`
    background: ${props => props.selected === true ? props.theme.grey : ""};
    min-height: 5rem;
    width: 90%;
    cursor: pointer;
    border-radius: 1rem;
    border: 2px solid grey;
    padding: .4rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    transition: .5s ease-in-out;
    &:hover{
        color: ${({ theme }) => theme.main};

    }
  

    .avatar{
        width: 3em;
        height:3em;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            width: 100%;
            height: 100% ;
            border-radius: 100%;
            object-fit: cover;

        }
    }

   
   

   
}
`






function Contact({ contacts, chooseChatDisplayed }) {
    const { user } = useStateVal()
    const [selected, setSelected] = useState(null)
    const [search, setSearch] = useState([])
    // console.log('contacts', contacts);

    const handleSelect = (index, contact) => {
        // console.log('index', index);
        // console.log('contact', contact);
        setSelected(index)

        chooseChatDisplayed(contact)
    }
    // console.log('selected', selected);
    const keys = ["name"]

    const handleSearch = (e) => {
        const filteredPost = contacts.filter(contact => {
            return keys.some((key => contact[key].toLowerCase().includes(e.target.value.toLowerCase())))
        })
        // console.log(filteredPost);
        setSearch(filteredPost)

    }


    return <>
        <Container>
            <div className="input">
                <input type="text" placeholder="Search for friends" className="chatcontactinput" onChange={e => handleSearch(e)} />
            </div>

            <div className="contacts">
                {
                    user && contacts && contacts.map((contact, index) => (
                        // user && search && search.map((contact, index) => (
                        //    [...Array(20)].map((contact, index) => (
                        <ContactContainer selected={index === selected} key={index} onClick={() => handleSelect(index, contact)}>
                            <div className="avatar">
                                <img src={contact.avatar ? contact.avatar : image} alt="avatar" />

                            </div>

                            <div className="name">

                                <h3>{contact.name}</h3>
                            </div>
                        </ContactContainer>
                    )
                    )
                }

            </div>

            <div className="currentuser">
                <div className="avatar">
                    <img src={user.avatar ? user.avatar : image} alt="avatar" />
                </div>
                <div className="name">

                    <h3>{user.name}</h3>
                </div>
            </div>


        </Container>

    </>
}

export default Contact