import styled from "styled-components"
import { useStateVal } from "../import"

const AlertContainer = styled.div`
background: ${(props => props.status === 'danger' ? "red" : props.status === 'success' ? "green" : "none")};
color: white;
padding:1rem;
width: 100%;
display: ${(props => props.status === 'danger' ? "flex" : props.status === 'success' ? "flex" : "none")};
// display:  "flex";
flex-direction: column;
align-items: center;
justify-contents: center;
// border: 2px solid red;
position: absolute;
top: 3rem;
z-index: 3;


`

export default function Alert() {
    const { alert } = useStateVal()
    const { message, status } = alert

    const Message = (message) => {
        if (typeof message === "object") {
            let messages = Object.values(message)
            return messages.map((msg, id) => (
                <div key={id}>
                    {msg} <br />
                </div>
            ))

        } else {
            return message
        }
    }

  


    return <AlertContainer status={status}>
        {Message(message)}
    </AlertContainer>
}