import styled from "styled-components"
import { ChatInput, Messages} from '../import'
import {  useState} from "react"

const Container = styled.div`
width: 100%;
height: 100%;
display: grid;
grid-template-rows: 88% 12%;
// border: 5px solid red;
overflow: hidden;
position:relative;
// background: ${({ theme }) => theme.main};


.chatmessage{
    padding: 1rem 2rem; 
    // border: 1px solid yellow;
    overflow:auto;
    &::-webkit-scrollbar{
        display: none
       
    }

    @media(max-width: 700px) {
        padding: .5rem; 

    
    }  
    
}

.input{
    z-index:100;
    width:100%;
    position:absolute;
    bottom:0;
}

`

function ChatBox({ currentChat}) {
    const [messages, setMessages] = useState([])
    const [typing, setTyping] = useState("")

 

    return <Container>
       
        <div className="chatmessage">
            <Messages currentChat={currentChat} messages={messages} setMessages={setMessages}/>
        </div>
        <div className="input">
            <ChatInput currentChat={currentChat} messages={messages} setMessages={setMessages} typing={typing} setTyping={setTyping}/>
        </div>
    </Container>
}

export default ChatBox