import axios from "axios"
import { useEffect, useRef } from "react"
import styled from "styled-components"
import { useStateVal } from "../import"
import { getMsg } from "../utils/Api"
import { LightTheme } from "../styles/globalStyles"

const Container = styled.div`
width: 100%;
// height: 100%;
// border: 2px solid green;
display: flex;
flex-direction: column;



`

const Box = styled.div`
display: flex;
justify-content: ${props => (props.self === true) ? "flex-end" : "flex-start"};
// border: 2px solid red;
margin: .3rem 0;




`
const Content = styled.div`

max-width: 40%;
overflow-wrap: break-word;
border-radius:${props => (props.self === true) ? "5px 15px" : "15px 5px"} ;
padding: .6rem;
font-size: 1.1rem;
background:  ${props => (props.self === true) ? "grey": props.theme.default};
color:   ${({ theme }) => theme.text};

.content{
    &>:last-child{
        font-size: .8rem;
        color: ${props => (props.theme === LightTheme) ? "white" : 'yellow'};
    }
}

@media(max-width: 700px) {
    max-width: 60%;
    font-size: .8rem;
    padding: .5rem;


    .content{
        &>:last-child{
            font-size: .6rem;
        }
    }

}  

`
export default function Messages({ currentChat, messages, setMessages }) {
    const { user } = useStateVal()
    const scrollRef = useRef()

    const getMessages = async () => {
        try {
            const { data } = await axios.post(getMsg, {
                from: user._id,
                to: currentChat._id
            })
            // console.log(data);
            setMessages(data.data)
        } catch (error) {
            console.log(error);

        }
    }

    useEffect(() => {
        getMessages()
    }, [currentChat])

    useEffect(() => {

        scrollRef.current?.scrollIntoView({ behaviour: "smooth" })
    }, [messages])

    // console.log(messages);

    return <Container>
        {
            messages.map((message, index) => (
                <Box key={index} self={message.fromSelf} ref={scrollRef}>
                    <Content self={message.fromSelf} >
                        <div className="content">
                            <p>{message.message.text}</p>
                            <p>{message.date}</p>
                        </div>
                    </Content>
                </Box>
            ))
        }

    </Container>
}