import styled from "styled-components"
import { useStateVal } from "../import"
import image from '../assets/user.png'

const Container = styled.div`
width: 100%;
height:100%;
// border: 1px solid red;
display : flex;
flex-direction: column;
align-items: center;
justify-content: center;
color: ${({ theme }) => theme.text};
// background: ${({ theme }) => theme.white};
background: #1F6357;
gap:1rem;

div:nth-of-type(1){
    height: 10rem;
    width:10rem;



    img{
        width:100%;
        height:100%;
        object-fit: cover;
        border-radius: 100%;

    }

    @media(max-width: 700px) and (max-width: 1200px){
        height: 5rem;
        width:5rem;     
    }
}

div:nth-of-type(2){
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    // border: 3px solid green;
    gap:.5rem;

    @media(max-width: 700px) and (max-width: 1200px){
       
    }
}

h1{
    font-size:3rem;

}


    span{
        font-size:2rem;
   
    }


@media(max-width: 700px) and (max-width: 1200px){
    h1{
        font-size: 1.5rem

    } 

    span{
        font-size: .7rem;
        margin: 1rem 0;

    }

}

`

function Welcome() {

    const { user } = useStateVal()
    return <Container>
        <div>
        <img src={user.avatar ? user.avatar : image} alt="avatar" />

        </div>
        <div>
            <h1>Welcome </h1>
            <span>{user?.name?.split(" ")[0]} </span>

        </div>
    </Container>
}

export default Welcome