import axios from "axios"
import { useEffect, useState } from "react"
import styled from "styled-components"
import image from '../assets/user.png'
import { useStateVal } from "../import"
import { getAllUser } from "../utils/Api"
// import { getAllUsers, getConversationbtwTwo } from '../utils/Api'

const Container = styled.div`
height: 100%;
background: ${({ theme }) => theme.body}}
padding: 1rem;
overflow: hidden;
color:   ${({ theme }) => theme.text};
border-left: 1px solid rgba(255, 255, 255, 0.2);

.header{
    // border: 2px solid red;
    text-align: center;
    padding: .5rem 0;
    height: 8%;
    color:   ${({ theme }) => theme.main};


    h3{
        font-size: .8rem;
        font-weight: 800;
    
    }
}



.chatcontainer{
    display: flex;
    flex-direction: column;
    gap: 1rem; 
    height: 92%;
    overflow: hidden;
    overflow-y: scroll;
    padding: 1rem 0;
    // border: 4px solid green;


    h3{
        font-size: .8rem;
    
    }
    .chatonlineuser{
        width: 100%;
        padding: 1rem;
        display: flex;
        gap: 1rem;
        align-items: center;
        border-bottom: 1px solid grey; 
        transition: .5s ease-in-out;
   
        .chatonlineimg{
            width: 3rem;
            height:3rem;
            position:relative;
            
            img{
                width:100%;
                height:100%;
                border-radius: 100%;
                object-fit: cover;
            
            }
        
            .onlinebadge{
                position: absolute;
                top:0;
                right:0;
                border-radius: 100%;
                width: .8rem;
                height:.8rem;
                background:green;
            }
        }
    
        &:hover{
            color:   ${({ theme }) => theme.main};

    
        }
    }


    &::-webkit-scrollbar{
        // display: none
        width: .2rem;
        &-thumb{
            background:  ${({ theme }) => theme.text}};
            width: .3rem;
    
    
        }
    }

}





`

export default function ChatOnlineUsers({ contacts, chooseChatDisplayed }) {
    const { onlineUsers} = useStateVal()
    const [allUsers, setAllUsers] = useState([])
    const [online, setOnline] = useState([])

    // console.log(user);

    const getAll = async() => {
        try {
            const {data} = await axios.get(getAllUser)
            // console.log('alluser', data.users);
            setAllUsers(data.users)
            
        } catch (error) {
            console.log(error);
        }
    }

    useEffect(() => {
        getAll()
    }, [])


    useEffect(() => {
        if (onlineUsers && allUsers) {
            setOnline(allUsers.filter(u => onlineUsers.some(o => o.userId === u._id)))
            // console.log('running');

        }
    }, [onlineUsers, allUsers])


    // console.log('onlineUsers', onlineUsers);
    // console.log('online', online);
    // console.log('allUsers', allUsers);

    const handleClick = (contact) => {
        chooseChatDisplayed(contact)

    }


    return (
        <Container>
            <div className="header">
                <h3>online friends</h3>
            </div>


            <div className="chatcontainer">


                {
                 online.map((val, id) => (
                        <div className="chatonlineuser" key={val._id} onClick={() => handleClick(val)}>
                            <div className="chatonlineimg">
                                <img src={val.avatar ? val.avatar : image} alt="" />
                                <div className="onlinebadge"></div>
                            </div>
                            <div className="onlinename">
                                <h3>{val?.name}</h3>

                            </div>

                        </div>

                    ))

                }
            </div>



        </Container>





    )

} 