import UseAnimation from '../utils/UseAnimation'
import { useState } from "react";
import '../styles/Form.css'
import { registerRoute } from '../utils/Api';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { useStateVal, Loader } from '../import';

const initialState = {
    name: "",
    email: "",
    password: "",
    confirmPassword: ""
}

export default function Register() {

    const [show, setShow] = useState(false)
    const [pass, setPass] = useState(false)
    const [visibleForm, showVisibleForm] = useState(false)
    const toggle = () => showVisibleForm(!visibleForm)
   const {setUser, setAlert, Timer } = useStateVal()
   const [loading, setLoading] = useState(false)

    //the form animation
    UseAnimation()
    let navigate = useNavigate()




    //signup
    const [formData, setFormData] = useState(initialState)
    const { name, email, password, confirmPassword } = formData


    const handleSubmit = async(e) => {
        e.preventDefault()
        setLoading(true)

        try {
            if(password !== confirmPassword){
                setLoading(false)

                setAlert({
                    message: "password dont match",
                    status: 'danger'
                })
                Timer()
                return;
            }
            const { data } = await axios.post(registerRoute, formData)
            // console.log(data);
            // console.log(data.data);
            if (data.success) {
                // console.log('if statement');
                setUser(data.data)
                setLoading(false)
                setFormData({ name: "", email: "", password: "", confirmPassword: "" })
                sessionStorage.setItem("detail", JSON.stringify(data.data))
                navigate("/chat")

            }
        } catch ({ response }) {
            // const { data } = response
            console.log(response);
            setLoading(false)
            setAlert({
                message: response.data.message,
                status: 'danger'
            })
            Timer()
           
        }
       

    }


    return (
        <>
                {loading && <Loader/>}
            <div className="form__container sign__up__form">

                <div className="img__box sign__up__imgbox">
                    <div className="sliding__link">
                        <p>Already a user?</p>
                        {/* <a href="!#" className='signup__btn' onClick={toggle}>Sign in</a> */}
                        <button className='signin__btn' onClick={toggle}>Sign in</button>

                    </div>
                    {/* <img src={image2} alt="" /> */}
                </div>

                <div className="form__box sign__up__box">
                    <h2>Sign up</h2>

                    <div className="login__with">
                        <div className="other__options">
                            {/* <a href="!#">
                                <img src={flower} alt="" />
                            </a>

                            <a href="!#">
                                <img src={motif} alt="" />
                            </a>

                            <a href="!#">
                                <img src={child} alt="" />
                            </a> */}
                        </div>
                        <p className='text'>Or sign up with ...</p>


                    </div>

                    <form onSubmit={handleSubmit}>
                        <div className="field">
                            <i className=''></i>
                            <input
                                type="text"
                                name="name"
                                placeholder="Full name"
                                value={name}
                                onChange={(e) => setFormData({ ...formData, [e.target.name]: e.target.value })}
                                required
                            />

                        </div>

                        <div className="field">
                            <i className=''>@</i>
                            <input
                                type="email"
                                placeholder="Email ID"
                                name="email"
                                value={email}
                                onChange={(e) => setFormData({ ...formData, [e.target.name]: e.target.value })}
                                required
                            />

                        </div>

                        <div className="field">
                            <i className=''></i>
                            <input
                                type={show ? "text" : "password"}
                                placeholder="Password"
                                name='password'
                                value={password}
                                onChange={(e) => setFormData({ ...formData, [e.target.name]: e.target.value })}
                                required
                            />
                            <div className="eye__btn">
                                <i className='' onClick={() => setShow(!show)}></i>
                            </div>

                        </div>

                        <div className="field">
                            <i className=''></i>
                            <input
                                type={pass ? "text" : "password"}
                                placeholder="Confirm password"
                                name='confirmPassword'
                                value={confirmPassword}
                                onChange={(e) => setFormData({ ...formData, [e.target.name]: e.target.value })}
                                required
                            />
                            <div className="eye__btn">
                                <i className='' onClick={() => setPass(!pass)}></i>
                            </div>


                        </div>

                        <button className='submit__btn'>Sign up</button>

                    </form>




                </div>



            </div>

        </>
    )

}