import { createContext, useContext, useEffect, useState, useRef } from "react";
import { useDarkMode } from "./useDarkMode";
import { DarkTheme, LightTheme } from '../styles/globalStyles';
import { io } from 'socket.io-client'


const StateContext = createContext()

function StateProvider({ children }) {
    const [alert, setAlert] = useState({ message: "", success: "" })
    const { theme, Toggle } = useDarkMode()
    const themeMode = (theme === "dark") ? DarkTheme : LightTheme
    const [arrivedMessage, setArrivedMessage] = useState("")
    const [typing, setTyping] = useState("")
    const [onlineUsers, setOnlineUsers] = useState([])
    const [user, setUser] = useState({})
    const [notification, setNotification] = useState([])
    const [ham, setHam] = useState(false)

    const socket = useRef()
    let onceRef = useRef(false)

  
    const Timer = () => {
        setTimeout(() => setAlert({
            message: "",
            status: ""
        }), 2000)
    }



    useEffect(() => {
        if(onceRef.current) return;

        setUser(JSON.parse(sessionStorage.getItem("detail")))
        // socket.current= io('ws://localhost:5000')
        socket.current= io('https://friendsocket.herokuapp.com')
        

        onceRef.current = true
        
    }, [])


    useEffect(() => {
        user && socket.current.emit("adduser", user?._id)
        socket.current.on("getUsers", data => {
            // console.log(data);
            setOnlineUsers(data)
        })

     
    }, [user])

//    console.log('onlineUsers', onlineUsers);

    return <StateContext.Provider value={{

        theme,
        themeMode,
        Toggle,
        user,
        alert,
        socket,
        setAlert,
        arrivedMessage,
        setArrivedMessage,
        typing,
        setTyping, 
        setUser,
        onlineUsers,
        setOnlineUsers, 
        notification, 
        setNotification,
        ham,
        setHam,
        Timer

    }}>
        {children}

    </StateContext.Provider>

}

export const useStateVal = () => useContext(StateContext)
export default StateProvider