import { useMemo, useRef, useState } from "react"
import styled from "styled-components"
import { Alert, useStateVal, Loader } from '../import'
import axios from 'axios'
import { upload , getUser} from "../utils/Api"
import avatar from '../assets/user.png'

const Container = styled.div`
width: 100%;
height:100vh;
background: ${({ theme }) => theme.body};
display: flex;

@media(max-width: 700px){
    padding-top: 6rem;
    flex-direction: column;
    overflow: auto;

}

`

const Box = styled.div`
width: 50%;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
// border: 2px solid red;

@media(max-width: 700px){
    width: 100%;

}


.main{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    @media(max-width: 700px){
        align-items: center; 
        border: 1px solid ${({theme}) => theme.grey};
        z-index: 3;
        padding: 1rem;
   
    }

    .image{
        width: 15rem;
        height: 15rem;
    
    
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 100%;
        }


        @media(max-width: 700px){
            width: 10rem;
            height: 10rem; 
        
        }
    }




    .name{
        // border: 2px solid red;
        display: flex;
        justify-content: space-between;

        h1{
            text-transform: capitalize;
            color: ${({ theme }) => theme.grey};
            font-size: 1rem;
            display: flex;
            flex-direction: column;
            gap: .5rem;
    

            span{
                font-size: 1rem;
                text-transform: uppercase;
                color: ${({ theme }) => theme.text};

                @media(max-width: 700px){
                    font-size: .6rem;
        
                }
        
            }

            @media(max-width: 700px){
                align-items: center;    
                font-size: .8rem;
    
            }
        

        }
    }
}





form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
    border-radius: 2rem;
    background: ${({ theme }) => theme.block};
    padding: 3rem 5rem;



    input{
        background: transparent;
        padding: 1rem;
        border: 0.1rem solid  ${({ theme }) => theme.text};
        border-radius: .4rem;
        color: white;
        width:100%;
        font-size:1rem; 

    }

    button{
        color: ${({ theme }) => theme.body};
        padding: 1rem 2rem;
        background: ${({ theme }) => theme.text};
        font-weight: bold;
        cursor: pointer;
        text-transform:uppercase;
        border: 0.1rem solid  ${({ theme }) => theme.body};
        border-radius: .4rem;
        font-size:1rem; 
        outline: none;
        transition: .4s ease-in-out;
        &:hover{
            color: ${({ theme }) => theme.main};
           
    
        }
    }

    .upload{
        height:12rem;
        width:12rem;
        display: flex;
        justify-content: center;
        align-items: center;
        padding:.3rem;
        border: 0.4rem solid white;
        margin: 0 auto;
        background: white;
        overflow:hidden;
        
        img{
            width:100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
            border: 0.1rem solid transparent;
            padding:.1rem;

        }
    }


    .title{
        display: flex;
        align-items:center;
        justify-content: center;
        color: ${({ theme }) => theme.regular};
        h1{
            text-transform: capitalize;
            color: ${({ theme }) => theme.text};
            font-size: 1rem;
            display: flex;
            flex-direction: column;
            gap: .5rem;
        }

    }
    
}

    

`


export default function Profile() {

    const { user, setUser, Timer } = useStateVal()

    const [image, setImage] = useState(null)
    const [preview, setPreview] = useState(null)
    const [loading, setLoading] = useState(false)

    const { setAlert } = useStateVal()
    let inputRef = useRef()

    useMemo(() => {
        if (!image) return
        const objectUrl = URL.createObjectURL(image)
        setPreview(objectUrl)
        return () => URL.revokeObjectURL(objectUrl)

    }, [image])


     const getData = async () => {
    try {

      const { data } = await axios.get(getUser)

    //   console.log("user", data);
      setUser(data.user)
      sessionStorage.setItem("detail", JSON.stringify(data.user))

    } catch (error) {
      console.log(error);

    }
  }

    const handleSubmit = async (e) => {
        e.preventDefault()
        setLoading(true)

        try {
            if (!image) {
                setLoading(false)

                setAlert({ message: "upload is empty", status: "danger" })
                Timer()
                return false
            }

            // console.log(image);

            const formData = new FormData()
            formData.append('avatar', image)
            const { data } = await axios.post(upload, formData)
            // console.log('data', data);
            if (data.success === true) {
                sessionStorage.setItem("detail", JSON.stringify(data.data))
                setLoading(false)
                setImage("")
                inputRef.current.value = ""
                setPreview("")
                getData()
            //     // window.location.assign('/chat')
            }
        } catch (error) {
            setLoading(false)
            console.log(error)

        }

    }
    const handleChange = e => {
        setImage(e.target.files[0])
    }

    // console.log("user");
    return <Container>
                        {loading && <Loader/>}

        <Alert />

        <Box>
            <div className="main">
                <div className="image">
                <img src={user.avatar ? user.avatar : avatar} alt="avatar" />
                </div>
                <div className="name">
                        <h1>name <span>{user?.name} </span></h1>

                </div>

                <div className="name">
                    <h1>status <span>available</span> </h1>

                </div>
                <div className="name">
                    <h1>email <span>{user?.email}</span> </h1>
                </div>

            </div>

        </Box>
        <Box>
            <form onSubmit={handleSubmit} encType="multipart/form-data">
                <div className="title">
                    <h1>Upload an avatar</h1>
                </div>
                <input ref={inputRef} type="file" name="avatar" placeholder="avatar" onChange={e => handleChange(e)} />
                {preview && <div className="upload">
                    <img src={preview} alt="avatar" />
                </div>
                }
                <button>Upload</button>


            </form>

        </Box>



    </Container>
}