import axios from "axios"
import { NavLink } from "react-router-dom"
import styled from "styled-components"
import { useStateVal, Logo } from "../import"
import { logout } from "../utils/Api"
import { Moon, Sun, Message, Notifications } from "../utils/svg"
import { motion } from 'framer-motion'
import image from '../assets/user.png'
import { useState } from "react"

const NavContainer = styled.nav`
position: fixed;
padding: .5rem 2rem;
top:0;
width: 100%;
// border: 1px solid black;
background: ${({ theme }) => theme.body};
color: ${({ theme }) => theme.text};
z-index:5;

@media (max-width: 700px){
    padding: 1rem 0.5rem;


}

`
const Nav = styled.ul`
display: flex;
align-items: center;
padding:0;
margin:0;
list-style-type: none;
gap: 1rem;

    .logo{
        display: flex;
        align-items: center;
        gap: .5rem;
    }

    &>:nth-child(2){
        margin-left: auto;
    }


    .notif{
        width: 22%;
        height: 150px;
        position: absolute;
        top: 70%;
        left:55%;
        z-index:6;
        background: ${props => props.theme.text};
        z-index: 5;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: ${props => props.theme.body};

        .notif__content{
            width: 100%;
            height: 100px;
            overflow-y:scroll;
            text-align: center;

            &::-webkit-scrollbar{
                // display: none
                width: .2rem;
                &-thumb{
                    background: ${props => props.theme.main};
                    width: .1rem;
        
        
                }
            }

            p{
                font-size: .7rem;
                margin: .5rem 0;
            }

        }


        button{
            padding: .5rem;
            width: 5rem;
            outline: none;
            border: none;
            cursor: pointer;
            background: ${props => props.theme.main};

            &:hover{ 
                color: ${props => props.theme.text};

            } 
        }

        @media (max-width: 700px){
            height: 100px;

            button{
                padding: .2rem;
                width: 3rem;

            }
   
        }



    }

    @media (max-width: 700px){
        justify-content: space-between;
    
        .chat{
            display: none;
        };

        .notif{
            width: 60%;
            left:50%;
            right:50%;
            transform: translateX(-50%)

        }


    }

    @media (min-width: 700px){
       
        .logo{
            &>:first-child{
                display: none;
            }

        }


    }
    




`

const DropDown = styled.button`

    font-size: 1rem;    
    border: none;
    outline: none;
    padding: .5rem 1rem;
    font-family: inherit;
    margin: 0;
    background:${({ theme }) => theme.body};
    color:${({ theme }) => theme.text};
    // border: .5px solid  ${({ theme }) => theme.text};
    display:flex;
    flex-direction:column;
    align-items: center;
    position:relative;
    cursor: pointer;

    .name{
        display:flex;
        align-items: center;
        gap: .5rem;

        @media (max-width: 700px){

            span{
                display:none;
            
        
            }
        }

    }

     &:hover{ 
        .dropdown__content {
            display: block;
        }
    } 


    .dropdown__content{
    
        display: none;
        position: absolute;
        top:100%;
        background:${({ theme }) => theme.body};
        width: 12rem;
        padding: .5rem 1rem;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        z-index: 1;
        

         a {
            float: none;
            color:${({ theme }) => theme.text};
            padding: 12px 16px;
            text-decoration: none;
            display: block;
            text-align: center;
            // border: 2px solid red;
          }

          
          .logout{
            padding: 12px 16px;
        }

    }

    .dropdown__chat{
        display: none
    }

    @media (max-width: 700px){
        font-size: .6rem;    
        padding: .5rem;

        
        .dropdown__content{
            width: 8.5rem;

            .logout{
                font-size: .7rem;    
      
            }

            a{
                font-size: .7rem;    

            }

 
        }

        .dropdown__chat{
            display: block
        }

       


    }

`

export const NavList = styled(motion.li)`
cursor: pointer;
text-transform: uppercase;
font-size: .8rem;
a{
    text-decoration: none;
    color:${({ theme }) => theme.text};
    font-size: .8rem;
}

`

const Avatar = styled.div`
width: 2.5rem;
height:2.5rem;

@media (max-width: 700px){
    width: 2rem;
    height:2rem;
}

img{
    width:100%;
    height:100%;
    object-fit: cover;
    border-radius: 100%;
}



`
const Notification = styled.div`
display: flex;
align-items: center;
justify-content: space-between;
gap: .5rem;
margin: 0 .5rem;


div{
    position: relative;

    .counter{
        width: 1rem;
        height: 1rem;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        background: red;
        border-radius: 50%;
        text-align:center;
        color:white;
        font-weight: 600;
        font-size: .8rem;
        top: -5px;
        right: 1rem;

        @media (max-width: 700px){
            font-size: .5rem;
        }
    }

   
}





@media (max-width: 700px){
    margin: unset;

    svg{
        width: 20px;
        height: 20px;
    }
   
}

`

const Hamburger = styled.div`
width: 1.5rem;
height: 1.5rem;
display: flex;
flex-direction: column;
justify-content: center;
gap: .2rem;

.line{
    width: 100%;
    height: 3px;
    background:${({ theme }) => theme.text};

}




`


// &>:last-child{}


export default function Navbar() {
    const { Toggle, user, theme, notification, setNotification, ham, setHam } = useStateVal()
    const [readNotifiaction, setReadNotification] = useState(false)
    const [readMessage, setReadMessage] = useState(false)
    const ToggleHam = () => setHam(!ham)

    const logOut = async () => {
        try {
            sessionStorage.removeItem("detail")
            await axios.get(logout)
            window.location.assign("/")

        } catch (error) {
            console.log(error);
        }
    }



    // console.log('notification', notification);

    const ToggleNoticication = (type) => {
        if (type === "notif") {
            setReadNotification(!readNotifiaction)
            setReadMessage(false)

        } else {
            setReadMessage(!readMessage)
            setReadNotification(false)

        }

    }


    return <>
        <NavContainer>
            <Nav>
                <div className="logo">

                    {/* {user &&  */}
                    <NavList
                        whileHover={{ scale: 1.1 }}
                        whileTap={{ scale: .9 }}
                        onClick={ToggleHam}
                    >

                        <Hamburger>
                            <div className="line" />
                            <div className="line" />
                            <div className="line" />
                        </Hamburger>
                    </NavList>
                    {/* } */}

                    <NavList
                        whileHover={{ scale: 1.1 }}
                        whileTap={{ scale: .9 }}>
                        <NavLink
                            to="/"
                            className={({ isActive }) => isActive ? "active" : undefined}>
                            <Logo fill="#FEAD01" />
                        </NavLink>
                    </NavList>
                </div>



                {!user && <NavList
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: .9 }} >
                    <NavLink to="register"
                        className={({ isActive }) => isActive ? "active" : undefined}>
                        register
                    </NavLink>
                </NavList>}


                {user && <NavList
                    className="chat"
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: .9 }} >
                    <NavLink to="chat"
                        className={({ isActive }) => isActive ? "active" : undefined}>
                        Chat
                    </NavLink>
                </NavList>}







                {user && <Notification>
                    <div onClick={() => ToggleNoticication('notif')}>
                        <Notifications width="70" height="30" fill="currentColor" />
                        {notification.length > 0 && <div className="counter">{notification.length}</div>}


                    </div>

                    <div onClick={() => ToggleNoticication("msg")}>
                        <Message width="70" height="30" fill="currentColor" />
                        {notification.length > 0 && <div className="counter">{notification.length}</div>}


                    </div>
                </Notification>
                }

                {user && <DropDown>
                    <div className="name">
                        <span>{user?.name?.split(" ")[0]}</span>

                        <Avatar>
                            <img src={user.avatar ? user.avatar : image} alt="" />

                        </Avatar>

                    </div>
                    <div className="dropdown__content">
                        {user && <NavList
                            className="dropdown__chat"
                            whileHover={{ scale: 1.1 }}
                            whileTap={{ scale: .9 }} >
                            <NavLink to="chat"
                                className={({ isActive }) => isActive ? "active" : undefined}>
                                Chat
                            </NavLink>
                        </NavList>}
                        {user && <NavList
                            whileHover={{ scale: 1.1 }}
                            whileTap={{ scale: .9 }} >
                            <NavLink to="profile"
                                className={({ isActive }) => isActive ? "active" : undefined}>
                                Profile
                            </NavLink>
                        </NavList>}

                        {user && <NavList
                            className="logout"
                            whileHover={{ scale: 1.1 }}
                            whileTap={{ scale: .9 }}
                            onClick={logOut}>
                            logout
                        </NavList>}



                    </div>

                </DropDown>


                }

                <NavList className="toggle"
                    onClick={Toggle}>
                    {theme === "dark" ?
                        <Moon width="30" height="30" fill="currentColor" style={{ padding: 0 }} />
                        :
                        <Sun style={{ padding: 0 }} width="30" height="30" fill="currentColor" />}
                </NavList>


                {notification.length > 0 && readNotifiaction && (
                    <div className="notif" >
                        <div className="notif__content">

                            {notification.map((n, id) => (
                                <p key={id}>{`${n.senderName.split(' ')[0]} sent you a message`}</p>

                            ))}
                        </div>

                        <button onClick={() => {
                            setNotification([])
                            setReadMessage(false)
                            setReadNotification(false)
                        }}> x</button>
                    </div>
                )}

                {notification.length > 0 && readMessage && (
                    <div className="notif" >
                        <div className="notif__content">
                            {notification.map((n, id) => (
                                <p key={id}>{`${n.senderName.split(' ')[0]} sent you "${n.message}" `}</p>


                            ))}
                        </div>
                        <button onClick={() => {
                            setNotification([])
                            setReadMessage(false)
                            setReadNotification(false)
                        }}> x</button>

                    </div>
                )}


            </Nav>
        </NavContainer>
    </>
}