import Register from "./Register";
import UseAnimation from '../utils/UseAnimation'
import { useState } from "react";
import '../styles/Form.css'
import axios from "axios";
import { loginRoute } from "../utils/Api";
import { useNavigate } from "react-router-dom";
import { Alert, useStateVal , Loader} from "../import";
// import styled from "styled-components";

const initialState = {
    email: "",
    password: "",
}



export default function Login() {
    const [show, setShow] = useState(false)
    const [visibleForm, showVisibleForm] = useState(false)
    const toggle = () => showVisibleForm(!visibleForm)
    const [loading, setLoading] = useState(false)

    //the form animation
    UseAnimation()

    const [formData, setFormData] = useState(initialState)
    const { email, password } = formData
    const {setUser, setAlert, Timer} = useStateVal()


    let navigate = useNavigate()

    const handleSubmit = async(e) => {
        e.preventDefault()
        setLoading(true)
        try {
            const { data } = await axios.post(loginRoute, formData)
            // console.log(data);
            // console.log(data.data);
            if (data.success) {
                setUser(data.data)
                setLoading(false)
                setFormData({ name: "", email: "", password: "", confirmPassword: "" })
                sessionStorage.setItem("detail", JSON.stringify(data.data))
                navigate("/chat")

            }
        } catch ({ response }) {
            // const { data } = response
            // console.log(response);
            setLoading(false)
            setAlert({
                message: response.data.message,
                status: 'danger'
            })
            Timer()
           
        }
       
    }


    // console.log('user', user);


    // const Box = styled.div`
    // background: ${props => props.theme.body}
    
    
    
    
    // `

    return (
        <>
        {loading && <Loader/>}
        <Alert/>
            <div className="main__div">
                <div className="form__container sign__in__form">
                    <div className="form__box sign__in__box">
                        <h2>Login</h2>
                        <form onSubmit={handleSubmit}>
                            <div className="field">
                                <i className=''>@</i>
                                <input
                                    type="email"
                                    name='email'
                                    placeholder="Email ID"
                                    value={email}
                                    onChange={(e) => setFormData({ ...formData, [e.target.name]: e.target.value })}
                                    required
                                />

                            </div>

                            <div className="field">
                                <i className=''></i>
                                <input
                                    type={show ? "text" : "password"}
                                    name="password" placeholder="Password"
                                    value={password}
                                    onChange={(e) => setFormData({ ...formData, [e.target.name]: e.target.value })}
                                    required
                                />
                                <div className="eye__btn">
                                    <i className='' onClick={() => setShow(!show)}></i>
                                </div>

                            </div>

                            <div className="forgot__link">
                                <a href="!#">Forgot Password?</a>

                            </div>

                            <button className='submit__btn'>Submit</button>

                        </form>
                        <div className="login__with">
                            <p className='text'>Or login with ...</p>
                            <div className="other__options">
                                {/* <a href="!#">
                                    <img src={flower} alt="" />
                                </a>

                                <a href="!#">
                                    <img src={motif} alt="" />
                                </a>

                                <a href="!#">
                                    <img src={child} alt="" />
                                </a> */}
                            </div>


                        </div>



                    </div>

                    <div className="img__box sign__in__imgbox">
                        <div className="sliding__link">
                            <p>Don't have an account?</p>
                            {/* <a href="!#" className='signup__btn' onClick={toggle}>Sign up</a> */}
                            <button className='signup__btn' onClick={toggle}>Sign up</button>

                        </div>
                        {/* <img src={image1} alt="" /> */}
                    </div>

                </div>


                {/* signup form */}

                <Register />



            </div>
        </>
    )

}